import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Box, Button } from "@mui/material";

const NextMovie = ({ movie, posters }) => {
    const navigate = useNavigate();
    const [isNextDisabled, setIsNextDisabled] = useState(false); //    

    const handleBackToList = () => {
        navigate('/releases'); //      
    };

    const handleNextMovie = () => {
        const currentIndex = posters.findIndex(m => m.id === movie.id);
        const nextIndex = currentIndex + 1;

        if (nextIndex < posters.length) {
            const nextMovie = posters[nextIndex];
            navigate(`/profilemovies/${nextMovie.id}`); //    
        } else {
            setIsNextDisabled(true); //  ,    
        }
    };

    return (
        <Box display="flex" justifyContent="space-between" alignItems="center" mt={2}>
            <Button
                variant="outlined"
                onClick={handleBackToList}
                sx={{
                    flexGrow: 1,
                    marginRight: 1,
                    borderColor: 'gray',
                    color: 'black',
                    '&:hover': {
                        borderColor: 'gray',
                        color: 'gray',
                    },
                }}
            >
                Back to List
            </Button>
            <Button
                variant="outlined"
                onClick={handleNextMovie}
                disabled={isNextDisabled} //  ,  isNextDisabled  true
                sx={{
                    flexGrow: 1,
                    marginRight: 1,
                    borderColor: 'gray',
                    color: isNextDisabled ? 'lightgray' : 'black', //      
                    '&:hover': {
                        borderColor: 'gray',
                        color: isNextDisabled ? 'lightgray' : 'gray',
                    },
                }}
            >
                Next Movie
            </Button>
        </Box>
    );
};

export default NextMovie;